import {Component} from "react";
import {Modal} from 'antd'
import bless1 from '../../assets/bless1.png'
import bless2 from '../../assets/bless2.png'
import blessButton from '../../assets/bless-button.png'
import './index.css'

class Index extends Component {
  constructor(props) {
    super(props);
    this.state = {
      height: 0
    }
  }

  componentDidMount() {

  }

  bless = () => {
    if (this.state.height !== 0) {
      return
    }
    this.start()
  }

  start = () => {
    const {height} = this.state
    setTimeout(() => {
      if (height < 200) {
        this.setState({
          height: height + 1
        }, this.start)
      } else {
        Modal.success({
          title: ''
        })
        this.setState({
          height: 0
        })
      }
    }, 10)
  }

  render() {
    const {height} = this.state
    return (
      <div className="container">
        <div className="buddha-container">
          <img className="light" src={bless2} alt=""/>
          <img className="buddha" src={bless1} alt=""/>
        </div>
        <div className="candle-container" onClick={this.bless}>
          <div className="color-container" style={{height: height + 'px'}}>
            <img className="candle color" src={blessButton} alt=""/>
          </div>
          <img className="candle filter" src={blessButton} alt=""/>
        </div>
        <div className="button-container">
          <div className="button" onClick={this.bless}></div>
        </div>
      </div>
    )
  }
}

export default Index;
